.difficulty {
  margin-left: auto;
  margin-right: auto;
  width: 24px;
  height: 28px;
  position: relative; }
  .difficulty span.first, .difficulty span.second, .difficulty span.third {
    display: block;
    background-color: #CFD0D3;
    position: absolute;
    bottom: 0; }
  .difficulty span.first {
    width: 30%;
    height: 33%; }
  .difficulty span.second {
    margin-left: 34%;
    width: 30%;
    height: 66%; }
  .difficulty span.third {
    margin-left: 69%;
    width: 30%;
    height: 100%; }
  .difficulty span.active {
    background-color: #1D2E46; }
