.layout {
  height: 100%;
  display: flex;
  flex-direction: column; }

.layout .content {
  flex-grow: 1; }

.header {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Raleway"; }
  @media screen and (max-width: 650px) {
    .header {
      flex-direction: column;
      width: 100%; } }
  .header #KDSlogo {
    width: 10em;
    margin-right: 5%; }
    @media screen and (max-width: 650px) {
      .header #KDSlogo {
        width: 5em;
        margin-right: 1em; } }
  .header .headline {
    float: right;
    margin-bottom: 0.5em;
    font-weight: normal; }
    @media screen and (max-width: 650px) {
      .header .headline {
        float: right;
        font-size: 1.1em; } }
  .header .header-location {
    margin: 0em;
    font-size: 1em;
    font-weight: normal;
    color: #75787b; }
    @media screen and (max-width: 650px) {
      .header .header-location {
        font-size: 0.8em; } }
    .header .header-location #location-img {
      width: 1em;
      margin-right: 0.5em; }
  .header .header-subheadline {
    margin-top: 0em;
    font-size: 0.8em;
    font-weight: normal; }
    .header .header-subheadline .subheadline-link {
      text-decoration: none;
      color: #009681; }
    @media screen and (max-width: 650px) {
      .header .header-subheadline {
        font-size: 0.7em; } }
  .header #KDSheader {
    display: flex;
    flex-direction: column;
    align-items: center; }

.menuAndHeader {
  height: 100px; }
  @media screen and (max-width: 650px) {
    .menuAndHeader {
      display: flex;
      flex-direction: column-reverse;
      align-items: flex-end; } }
