@import url("https://fonts.googleapis.com/css?family=Raleway&display=swap");
.content .paragraphLink {
  color: #009681;
  font-weight: bold; }

@media screen and (min-width: 650px) {
  .content {
    margin-left: 20%;
    margin-right: 20%; } }

@media screen and (max-width: 650px) {
  #KDSheader * {
    display: none; } }

body {
  padding: 0;
  font-family: "Raleway", sans-serif;
  overflow-x: hidden; }

h1 {
  text-align: center;
  margin-bottom: 1em; }

.page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%; }

.document {
  margin-top: 90px;
  width: 40%;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #1d2e46;
  padding: 0 90px;
  background-color: white; }
  @media screen and (max-width: 650px) {
    .document {
      margin-top: 30px;
      width: auto;
      margin-left: 30px;
      margin-right: 30px;
      border: none;
      padding: 0 0; } }
  .document *:first-child {
    margin-top: 50px; }
  .document *:last-child {
    margin-bottom: 50px; }

.background {
  z-index: -1;
  margin-left: -50%;
  width: 200%;
  height: 100%;
  position: absolute; }
  @media screen and (max-width: 650px) {
    .background {
      display: none; } }
