.filterButton {
  cursor: pointer;
  position: absolute;
  top: 32px;
  left: 30px;
  width: 33px;
  display: block; }
  .filterButton span.top,
  .filterButton span.middle,
  .filterButton span.bottom {
    transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out, width 0.5s ease-in-out;
    display: block;
    height: 4px;
    margin-top: 4px;
    margin-bottom: 5px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    color: #75787b;
    background: currentColor;
    border-radius: 3px;
    z-index: 2; }
  .filterButton span.top {
    width: 100%; }
  .filterButton span.middle {
    width: 70%; }
  .filterButton span.bottom {
    width: 40%; }

.filterButton.open {
  z-index: 5; }
  .filterButton.open span.top {
    -webkit-transform: translateY(6px) rotateZ(45deg);
    -moz-transform: translateY(6px) rotateZ(45deg);
    -ms-transform: translateY(6px) rotateZ(45deg);
    -o-transform: translateY(6px) rotateZ(45deg);
    transform: translateY(6px) rotateZ(45deg); }
  .filterButton.open span.middle {
    opacity: 0; }
  .filterButton.open span.bottom {
    -webkit-transform: translateY(-12px) rotateZ(-45deg);
    -moz-transform: translateY(-12px) rotateZ(-45deg);
    -ms-transform: translateY(-12px) rotateZ(-45deg);
    -o-transform: translateY(-12px) rotateZ(-45deg);
    transform: translateY(-12px) rotateZ(-45deg);
    width: 100%; }

.filter {
  width: 100%;
  transform: translateX(-100%);
  z-index: 1;
  overflow-x: hidden;
  position: absolute;
  top: 0;
  left: 0;
  background: white;
  transform-origin: 0 0;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 0.5); }
  @media screen and (min-width: 650px) {
    .filter {
      width: 25%; } }
  .filter p {
    font-weight: bold;
    font-size: 16px; }
  .filter h1 {
    font-weight: 100;
    margin-top: 32px;
    font-size: 24px; }
  .filter .active {
    margin-left: 40px;
    margin-top: 56px; }
  .filter .nonActive {
    margin-top: 50px;
    margin-left: 40px; }

.filter-tag {
  border-radius: 20px;
  border: 2px solid #1d2e46;
  padding: 5px 7px;
  margin-right: 15px;
  margin-bottom: 20px;
  color: #1d2e46;
  display: inline-block;
  font-size: 14px; }

.filter-tag.selected {
  color: white;
  background-color: #1d2e46; }

.filter.open {
  z-index: 4;
  transform: none; }
