#menu {
  width: 100%;
  transform: translate(0, 0);
  display: flex;
  justify-content: center;
  font-family: "Raleway";
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  position: absolute;
  top: 0;
  left: 100vw;
  background-color: white;
  margin: 0;
  padding-bottom: 40px;
  /* to stop flickering of text in safari */
  transform-origin: 0 0;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 0.5); }
  @media screen and (min-width: 650px) {
    #menu {
      width: 25%;
      transform: translate(0, 0); } }
  #menu .menuHeader {
    margin-top: 32px;
    font-size: 24px;
    font-weight: 100; }
  #menu .link {
    color: #28334a;
    text-decoration: none;
    padding: 1em 1em 1em 1em;
    font-size: 24px;
    margin-left: 40px;
    font-weight: bold; }
    #menu .link:hover {
      transition: 0.4s linear all;
      color: black;
      cursor: pointer; }

#menuToggle input:checked ~ #menu {
  z-index: 4;
  transform: translate(-100%, 0); }

#menuToggle input:checked {
  z-index: 6; }

#menuToggle input:checked ~ .menuButton {
  z-index: 5; }

#menuToggle span.top,
#menuToggle span.middle,
#menuToggle span.bottom {
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  display: block;
  width: 100%;
  height: 4px;
  margin-top: 4px;
  margin-bottom: 5px;
  position: relative;
  color: #75787b;
  background: currentColor;
  border-radius: 3px;
  z-index: 2; }

#menuToggle input:checked ~ .menuButton span.top {
  -webkit-transform: translateY(6px) rotateZ(45deg);
  -moz-transform: translateY(6px) rotateZ(45deg);
  -ms-transform: translateY(6px) rotateZ(45deg);
  -o-transform: translateY(6px) rotateZ(45deg);
  transform: translateY(6px) rotateZ(45deg); }

#menuToggle input:checked ~ .menuButton span.middle {
  opacity: 0; }

#menuToggle input:checked ~ .menuButton span.bottom {
  -webkit-transform: translateY(-12px) rotateZ(-45deg);
  -moz-transform: translateY(-12px) rotateZ(-45deg);
  -ms-transform: translateY(-12px) rotateZ(-45deg);
  -o-transform: translateY(-12px) rotateZ(-45deg);
  transform: translateY(-12px) rotateZ(-45deg); }

#menuToggle input {
  display: block;
  position: absolute;
  top: 32px;
  right: 26px;
  width: 34px;
  height: 34px;
  cursor: pointer;
  opacity: 0;
  /* hide this */
  z-index: 3;
  /* and place it over the hamburger */
  -webkit-touch-callout: none; }

#menuToggle .menuButton {
  position: absolute;
  top: 32px;
  right: 30px;
  width: 33px;
  display: block; }

.register-button {
  display: block;
  text-align: center;
  width: 100%;
  padding: 15px 0px;
  background-color: #a0d1ca;
  color: black;
  text-decoration: none;
  font-size: 18px;
  white-space: nowrap;
  font-weight: bold;
  z-index: 2; }

.header .register-button {
  width: 85%; }

div.schedule div#menu a.schedule,
div.talksAndSpeakers div#menu a.talksAndSpeakers,
div.practicalities div#menu a.practicalities,
div.callForPresentations div#menu a.callForPresentations,
div.aboutUs div#menu a.aboutUs,
div.location div#menu a.location,
div.codeOfConduct div#menu a.codeOfConduct {
  color: #009681; }
