div.talksAndSpeakers {
  width: 100%;
  padding: 0;
  margin: 0; }
  div.talksAndSpeakers .talks-container {
    margin-bottom: 90px;
    margin-top: 90px; }
    @media screen and (min-width: 650px) {
      div.talksAndSpeakers .talks-container {
        border: 1px solid black; } }
    div.talksAndSpeakers .talks-container h1 {
      font-size: 36px; }
  div.talksAndSpeakers .talk-container .talk .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 30px; }
    div.talksAndSpeakers .talk-container .talk .header .room {
      margin-top: 0;
      position: relative; }
      div.talksAndSpeakers .talk-container .talk .header .room span {
        display: block;
        float: left; }
  div.talksAndSpeakers .talk-container .talk .day {
    font-weight: bold;
    font-size: 12px;
    margin-bottom: 6px; }
  div.talksAndSpeakers .talk-container .talk .time {
    font-weight: bold;
    font-size: 12px;
    margin-bottom: 6px; }
  div.talksAndSpeakers .talk-container .talk .title {
    font-size: 24px;
    font-weight: bold;
    text-align: left;
    margin-bottom: 7px; }
  div.talksAndSpeakers .talk-container .talk .speaker {
    font-size: 18px;
    color: #a0d1ca;
    text-align: left;
    margin-bottom: 20px; }
  div.talksAndSpeakers .talk-container .talk .info {
    font-size: 16px;
    margin-bottom: 30px; }
